.publicationsList{
    flex: 4;
    background-color: #FFE5D9 ;
    opacity: 0.8;
}

.publicationsListItem{
    display: flex;
    align-items: center;
}

.publicationsListImg {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
  }

.publicationsListEdit{
    border: none;
    border-radius: 10px;
    padding: 5px 10px;
    background-color: #F4ACB7;
    color: white;
    cursor: pointer;
    margin-right: 20px;
}

.publicationsListDelete{
    color: #9D8189;
    cursor: pointer;
}